import {
  SET_SCREAMS,
  LOADING_DATA,
  LIKE_SCREAM,
  UNLIKE_SCREAM,
  DELETE_SCREAM,
  POST_SCREAM,
  STOP_LOADING_UI,
  SET_SCREAM,
  SUBMIT_COMMENT,
} from "../types";
import axios from "axios";
import { loadingUi, setError, clearError } from "./userActions";

export const getScreams = () => (dispatch) => {
  dispatch(loadingData());
  axios
    .get("/screams")
    .then((res) => {
      dispatch(setScreams(res.data));
    })
    .catch((err) => {
      dispatch(setScreams([]));
    });
};

export const loadingData = () => ({
  type: LOADING_DATA,
});

export const setScreams = (data) => ({
  type: SET_SCREAMS,
  payload: data,
});

export const likeScream = (screamId) => (dispatch) => {
  axios
    .get(`/scream/${screamId}/like`, {
      headers: {
        Authorization: localStorage.getItem("FBIdToken"),
      },
    })
    .then((res) => {
      dispatch(likeScreamtype(res.data));
    })
    .catch((err) => console.log(err));
};

export const unlikeScream = (screamId) => (dispatch) => {
  axios
    .get(`/scream/${screamId}/unlike`, {
      headers: {
        Authorization: localStorage.getItem("FBIdToken"),
      },
    })
    .then((res) => {
      dispatch(unlikeScreamtype(res.data));
    })
    .catch((err) => console.log(err));
};

export const deleteScream = (screamId) => (dispatch) => {
  axios
    .delete(`/scream/${screamId}`)
    .then(() => {
      dispatch(deleteScreamtype(screamId));
    })
    .catch((err) => console.log(err));
};

export const postScream = (newScream) => (dispatch) => {
  dispatch(loadingUi());
  axios
    .post("/scream", newScream)
    .then((res) => {
      dispatch(postScreamtype(res.data));
      dispatch(clearError());
    })
    .catch((err) => {
      dispatch(setError(err));
    });
};

export const getScream = (screamId) => (dispatch) => {
  dispatch(loadingUi());
  axios
    .get(`/scream/${screamId}`)
    .then((res) => {
      dispatch(setScreamType(res.data));
      dispatch(stopLoadingUi());
    })
    .catch((err) => console.log(err));
};
//submit a comment
export const submitComment = (screamId, commentData) => (dispatch) => {
  axios
    .post(`/scream/${screamId}/comment`, commentData)
    .then((res) => {
      dispatch(submitCommentType(res.data));
      dispatch(clearErrors());
    })
    .catch((err) => {
      dispatch(setError(err));
    });
};

export const getUserData = (userHandle) => (dispatch) => {
  dispatch(loadingData());
  axios
    .get(`/user/${userHandle}`)
    .then((res) => {
      dispatch(setScreams(res.data.screams));
    })
    .catch((err) => {
      dispatch(setScreams(null));
    });
};

export const submitCommentType = (data) => ({
  type: SUBMIT_COMMENT,
  payload: data,
});

export const clearErrors = () => (dispatch) => {
  dispatch(clearError());
};

export const stopLoadingUi = () => ({
  type: STOP_LOADING_UI,
});

export const postScreamtype = (data) => ({
  type: POST_SCREAM,
  payload: data,
});

export const deleteScreamtype = (data) => ({
  type: DELETE_SCREAM,
  payload: data,
});

export const likeScreamtype = (data) => ({
  type: LIKE_SCREAM,
  payload: data,
});
export const unlikeScreamtype = (data) => ({
  type: UNLIKE_SCREAM,
  payload: data,
});

export const setScreamType = (data) => ({
  type: SET_SCREAM,
  payload: data,
});
