import React, { Component, Fragment } from "react";
import PropTypes from "prop-types";
import withStyles from "@material-ui/core/styles/withStyles";
import Button from "@material-ui/core/Button";
import { connect } from "react-redux";
import Paper from "@material-ui/core/Paper";
import MuiLink from "@material-ui/core/Link";
import { Link } from "react-router-dom";
import Typography from "@material-ui/core/Typography";
import LocationOn from "@material-ui//icons/LocationOn";
import LinkIcon from "@material-ui//icons/Link";
import CalendarToday from "@material-ui//icons/CalendarToday";
import dayjs from "dayjs";
import EditIcon from "@material-ui/icons/Edit";
import { uploadImage, logoutUser } from "../../redux/actions/userActions";
import KeyboardReturn from "@material-ui/icons/KeyboardReturn";
import EditDetails from "./EditDetails";
import MyButton from "../../util/MyButton";
import ProfileSkeleton from "../../util/ProfileSkeleton";

const styles = (theme) => ({
  paper: {
    padding: 20,
  },
  profileImage: {
    width: " 200px",
    height: "200px",
    objectFit: "cover",
    maxWidth: "100%",
    borderRadius: "50%",
    alignItem: "center",
  },
  profile: {
    "& .image-wrapper": {
      textAlign: "center",
      position: "relative",
      "& button": {
        position: "absolute",
        top: "80%",
        left: "70%",
      },
    },

    "& .profile-details": {
      textAlign: "center",
      "& span, svg": {
        verticalAlign: "middle",
      },
      "& a": {
        color: theme.palette.primary.main,
      },
    },
    "& hr": {
      border: "none",
      margin: "0 0 10px 0",
    },
    "& svg.button": {
      "&:hover": {
        cursor: "pointer",
      },
    },
  },
  buttons: {
    textAlign: "center",
    "& a": {
      margin: "20px 10px",
    },
  },
});

class Profile extends Component {
  handleImageChange = (event) => {
    const image = event.target.files[0];
    //send to server
    const formData = new FormData();
    formData.append("image", image, image.name);
    this.props.uploadImage(formData);
  };
  handleEditPicture = () => {
    const fileInput = document.getElementById("imageInput");
    fileInput.click();
  };

  handleLogout = () => {
    this.props.logoutUser();
  };

  render() {
    const {
      classes,
      user: { loading, userCredentials, authenticated },
    } = this.props;
    let profileMarkup = !loading ? (
      authenticated ? (
        <Paper className={classes.paper}>
          <div className={classes.profile}>
            <div>
              <img
                className={classes.profileImage}
                src={userCredentials.imageUrl}
                alt="profile"
              />
              <input
                type="file"
                id="imageInput"
                hidden="hidden"
                onChange={this.handleImageChange}
              />
              <MyButton
                tip="edit profile picture"
                onClick={this.handleEditPicture}
                btnClassName="button"
              >
                <EditIcon color="primary" />
              </MyButton>
            </div>
            <hr />
            <div className="profile-details">
              <MuiLink
                component={Link}
                to={`/users/${userCredentials.handle}`}
                color="primary"
                variant="h5"
              >
                @{userCredentials.handle}
              </MuiLink>
              <hr />
              {userCredentials.bio && (
                <Typography variant="body2">{userCredentials.bio}</Typography>
              )}
              <hr />
              {userCredentials.location && (
                <Fragment>
                  <LocationOn color="primary" />
                  <span>{userCredentials.location}</span>
                  <hr />
                </Fragment>
              )}
              {userCredentials.website && (
                <Fragment>
                  <LinkIcon color="primary" />
                  <a
                    href={userCredentials.website}
                    target="_blank"
                    rel="noopener noreferrer"
                  >
                    {" "}
                    {userCredentials.website}
                  </a>
                </Fragment>
              )}
              <br />
              <CalendarToday color="primary" />{" "}
              <span>
                joined {dayjs(userCredentials.createdAt).format("MMM YYYY")}
              </span>
            </div>
            <MyButton tip="Logout" onClick={this.handleLogout}>
              <KeyboardReturn color="primary" />
            </MyButton>
            <EditDetails />
          </div>
        </Paper>
      ) : (
        <Paper className={classes.paper}>
          <Typography variant="body2" align="center">
            No profile found,Please login again
          </Typography>
          <div className={classes.buttons}>
            <Button
              variant="contained"
              color="primary"
              component={Link}
              to="/login"
            >
              Login
            </Button>
            <Button
              variant="contained"
              color="secondary"
              component={Link}
              to="/signup"
            >
              Signup
            </Button>
          </div>
        </Paper>
      )
    ) : (
      <ProfileSkeleton />
    );
    return profileMarkup;
  }
}

const mapStateToProps = (state) => ({
  user: state.user,
});

const mapDispatchToProps = (dispatch) => ({
  uploadImage: (data) => {
    dispatch(uploadImage(data));
  },
  logoutUser: () => {
    dispatch(logoutUser());
  },
});

Profile.propTypes = {
  user: PropTypes.object.isRequired,
  classes: PropTypes.object.isRequired,
  uploadImage: PropTypes.func.isRequired,
  logoutUser: PropTypes.func.isRequired,
};

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(Profile));
